const func = $target => {
    console.log('triggered');
    var $content = $target.find('input'),
        $button = $target.find('.btn');
    $button.on('click touch', function(e) {
		e.preventDefault();
        $content.select();
        document.execCommand('copy');
		// alert('Copied to clipboard');
		$('.alert').removeClass('d-none');
    });
};

export default func;