let apphashes = window.apphashes || {};

function getFilename(key) {
	if (ENV == 'production') {
		return key + '.' + apphashes[key] + '.js';
	} else {
		return key + '.js';
	}
}

function getAppKey(key) {
	if (window.appKeys) {
		return window.appKeys[key] || null;
	}
	return null;
}

