const func = $target => {
	var $modal = $('.modal#deletePaymentModal'),
		$input = $modal.find('input#paymentToken');

	$modal
		.on('show.bs.modal', function(e) {
			$input.val($(e.relatedTarget).data('id'));
		})
		.on('hidden.bs.modal', function(e) {
			$input.val('');
		});
};

export default func;
